import React, { useState } from 'react';
import CareerBtn from '../../../../../components/UI/Atoms/ButtonGroup/CareerBtn';
import InfoLabelMD from '../../../../../components/UI/Atoms/Info/InfoLabelMD';
import InfoEditInput from '../../../../../components/UI/Atoms/Input/InfoEditInput';
import DatepickerSM from '../../../../../components/UI/Atoms/Datepicker/DatepickerSM';
import FormGroup from '@mui/material/FormGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import CreateBtnMD from '../../../../../components/UI/Atoms/Button/CreateBtnMD';
import RegistrationAccordion from '../../RegistrationAccordion';
import { useToggle } from '../../../../../hooks/basic/useToggle';
import BlackBtnMD from '../../../../../components/UI/Atoms/Button/BlackBtnMD';
import { useDebug } from '../../../../../hooks/basic/useDebug';
import BlackBtnSM from '../../../../../components/UI/Atoms/Button/BlackBtnSM';

const AwardHistory = () => {
  const [expanded, Control] = useToggle();

  const form = {
    dojang_name: '',
    employment_start_date: '',
    employment_end_date: '',
  };
  const [list, setList] = useState([form]);

  const handleAdd = (e) => {
    setList([...list, form]);
  };

  const handleDelete = (e) => {
    let { id } = e.currentTarget;
    let temp = list.filter((v, idx) => {
      return id != idx;
    });
    setList(temp);
  };

  const handleChange = (e) => {
    let { id, name, value } = e.currentTarget;
    let temp = [...list];
    temp[id] = { ...temp[id], [name]: value };
    setList(temp);
  };

  const handleSelect = (e, index) => {
    let { name, value } = e.target;
    let temp = [...list];
    temp[index] = { ...temp[index], [name]: value };
    setList(temp);
  };

  const [alignment, setAlignment] = useState(false);

  return (
    <RegistrationAccordion title="수상 이력" expanded={expanded} onChange={Control}>
      <div>
        <div className="mb-[30px]">
          <CareerBtn alignment={alignment} setAlignment={setAlignment} />
        </div>
        {alignment &&
          React.Children.toArray(
            list?.map((item, idx) => (
              <div className="mb-[20px]">
                {idx !== 0 && (
                  <>
                    <div className="w-full h-[1px] bg-[#aaaaaa] mt-[10px] mb-[20px]"></div>
                    <div className="text-right mb-[10px]">
                      <BlackBtnSM id={`${idx}`} className="mb-[10px]" onClick={handleDelete}>
                        삭제
                      </BlackBtnSM>
                    </div>
                  </>
                )}
                <div className="flex flex-col gap-[10px]">
                  <div className="flex items-center justify-between">
                    <InfoLabelMD className="text-[16px] flex-none">수상 내역</InfoLabelMD>
                    <InfoEditInput
                      placeholder="수상 내역"
                      sx={{ '& .MuiInputBase-input': { height: '30px', width: '334px' } }}
                      id={`${idx}`}
                      name="dojang_name"
                      value={item.dojang_name}
                      onChange={handleChange}
                    ></InfoEditInput>
                  </div>
                  <div className="flex items-center justify-start gap-[10px]">
                    <InfoLabelMD className="text-[16px] w-[100px] flex-none">수상 날짜</InfoLabelMD>
                    <DatepickerSM
                      length={160}
                      sx={{ '& .MuiInputBase-root': { height: '40px' } }}
                      id={`${idx}`}
                      name="employment_start_date"
                      value={item.employment_start_date}
                      onChange={(e) => handleSelect(e, idx)}
                    ></DatepickerSM>
                    <input
                      value={item.employment_start_date}
                      name="employment_start_date"
                      hidden
                      readOnly
                    />
                  </div>
                </div>
              </div>
            )),
          )}
        {alignment && (
          <div className="text-center mt-[30px]">
            <CreateBtnMD className="w-[115px] text-[14px]" onClick={handleAdd}>
              + 이력 추가
            </CreateBtnMD>
          </div>
        )}
      </div>
    </RegistrationAccordion>
  );
};

export default AwardHistory;
