import React, { useState } from 'react';
import { Typography } from '@mui/material';
import PageContainer from '../../../../components/layout/PageContainer';
import SecondBtnSM from '../../../../components/UI/Atoms/Button/SecondBtnSM';
import PrimaryBtnSM from '../../../../components/UI/Atoms/Button/PrimaryBtnSM';
import Certificate from '../Common/Certificate';
import BasicInfo from '../Common/BasicInfo';
import Career from './AccordionInfo/Career';
import OtherCertificates from './AccordionInfo/OtherCertificates';
import { useNavigate } from 'react-router-dom';
import Header from '../../../../components/layout/Header';
import { useMutation } from '@tanstack/react-query';
import { createKwanjangInfo } from '../../../../api/Kwanjang/kwanjang';
import { useRecoilState } from 'recoil';
import userInfoAtom from '../../../../recoil/atom/login/userInfoAtom';
import { useFileInput } from '../../../../hooks/file/useFileInput';
import ProfileImage from '../../../../components/UI/Atoms/Profile/ProfileImage';
import ProfileEditUploadBtn from '../../../../components/UI/Atoms/Icon/ProfileEditUploadBtn';
import { getFormData } from '../../../../utils/basic/getFormData';
import { formDataDebug } from '../../../../utils/debug/debug';
import LoadingModal from '../../../../components/UI/Atoms/Spinner/LoadingModal';
import DriverLicense from '../Sabum/DriverLicense';
import AwardHistory from './AccordionInfo/AwardHistory';
import UnivInfo from '../Sabum/UnivInfo';

const KwanjangInfoRegistration = () => {
  const navigate = useNavigate();
  const [userInfo, setUserInfo] = useRecoilState(userInfoAtom);
  // useDebug(userInfo)

  // query
  const { mutate, isLoading } = useMutation((body) => createKwanjangInfo(body));

  // handler
  const handleSubmit = (e) => {
    // basicInfo
    let dob = document.getElementById('dob')?.value;
    let sex =
      document.getElementById('sex').textContent === '남자'
        ? '1'
        : document.getElementById('sex').textContent === '여자'
        ? '2'
        : '';

    // certificates
    let level = document.getElementById('level')?.textContent;
    let tkd_level_number = document.getElementById('tkd_level_number')?.value;
    let instructor_license_grade = document.getElementById('instructor_license_grade').textContent;
    let instructor_license_number = document.getElementById('instructor_license_number')?.value;

    // otherCertificates
    let otherCertLength = document.getElementsByName('license_name')?.length;
    let otherCertList = [];
    for (let i = 0; i < otherCertLength; i++) {
      let temp = {
        license_name: document.getElementsByName('license_name')[i]?.value,
        license_grade: document.getElementsByName('license_grade')[i]?.value,
        license_number: document.getElementsByName('license_number')[i]?.value,
      };
      otherCertList.push(temp);
    }

    // career
    let careerLength = document.getElementsByName('dojang_name')?.length;
    let careerList = [];
    for (let i = 0; i < careerLength; i++) {
      let startDate = document.getElementsByName('employment_start_date')[i]?.value;
      let endDate = document.getElementsByName('employment_end_date')[i]?.value;

      const temp = {
        dojang_name: document.getElementsByName('dojang_name')[i]?.value,
        employment_start_year: startDate?.slice(0, 4),
        employment_start_month: Number(startDate?.slice(5, 7)),
        employment_end_year: endDate?.slice(0, 4),
        employment_end_month: Number(endDate?.slice(5, 7)),
      };
      careerList.push(temp);
    }

    // values값들이 빈 스트링이면 alert창 띄우면서 return
    if (dob === '') {
      alert('생년월일을 입력해주세요.');
      return;
    }
    if (sex === '') {
      alert('성별을 선택해주세요.');
      return;
    }
    if (level === '') {
      alert('보유 단증의 등급을 선택해주세요.');
      return;
    }
    if (tkd_level_number === '') {
      alert('단증번호를 입력해주세요.');
      return;
    }
    if (instructor_license_grade === '') {
      alert('사범 등급을 선택해주세요.');
      return;
    }
    if (instructor_license_number === '') {
      alert('사범 자격증 번호를 입력해주세요.');
      return;
    }
    if (
      otherCertLength > 0 &&
      (license_name === '' || license_grade === '' || license_number === '')
    ) {
      alert('기타 단증 정보를 입력해주세요.');
      return;
    }
    if (
      careerLength > 0 &&
      (dojang_name === '' || employment_start_year === '' || employment_end_year === '')
    ) {
      alert('도장 운영 경력을 입력해주세요.');
      return;
    }

    // driverLicense
    let driver_license = document.getElementById('driver_license')?.value;
    let driver_license_obtaining_date = document.getElementById(
      'driver_license_obtaining_date',
    )?.value;

    if (userInfo?.id) {
      let temp = {
        user: userInfo?.id,
        dob: dob,
        sex: sex,
        level: level,
        tkd_level_number: tkd_level_number,
        instructor_license_grade: instructor_license_grade,
        instructor_license_number: instructor_license_number,
      };

      const data = [];
      data.push(...Object.entries({ ...temp }));

      if (driver_license && driver_license_obtaining_date) {
        let date = driver_license_obtaining_date;
        let driver_license_obtaining_year = date.slice(0, 4);
        let driver_license_obtaining_month = Number(date.slice(5, 7));
        data.push(['driver_license', driver_license]);
        data.push(['driver_license_obtaining_year', driver_license_obtaining_year]);
        data.push(['driver_license_obtaining_month', driver_license_obtaining_month]);
      }

      if (careerList.length != 0) {
        data.push(['kwanjang_career_info', JSON.stringify(careerList)]);
      }

      if (otherCertList.length != 0) {
        data.push(['kwanjang_license_info', JSON.stringify(otherCertList)]);
      }

      if (img?.img) {
        data.push(['img', img.img]);
      }

      const formData = getFormData(data);
      // formDataDebug(formData)

      mutate(formData, {
        onSuccess: (res) => {
          setLoading(true);
          setUserInfo({ ...userInfo, is_linked: 1 });
          setTimeout(navigate('/select/dojang'), 1500);
        },
        onError: (err) => {
          alert(err.response.data.message);
        },
      });
    } else {
      alert('오류가 발생하였습니다. 로그아웃 후 다시 로그인 해주세요.');
    }
  };

  const [loading, setLoading] = useState(false);
  // 기본정보

  const [img, setImg] = useState({
    previewUrl: '',
  });
  const [handleFileInput, fileInputRef, clickUpload] = useFileInput({
    data: img,
    urlKey: 'previewUrl',
    fileKey: 'img',
    set: setImg,
  });

  return (
    <>
      <Header temporary />
      <PageContainer width="sm">
        <Typography variant="h2" className="mt-[25px] mb-[50px]">
          관장님 정보 등록
        </Typography>
        <div className="w-[500px]">
          <div className="flex flex-col items-center relative mb-[30px]">
            <ProfileImage
              size="140px"
              previewUrl={img?.previewUrl}
              onChange={handleFileInput}
              ref={fileInputRef}
              full
            />
            <ProfileEditUploadBtn
              onClick={clickUpload}
              className="translate-x-[-11rem] translate-y-[0.4rem]"
            />
          </div>
          <BasicInfo />
          <UnivInfo />
          <Certificate collapse />
          <OtherCertificates />
          <Career />
          <AwardHistory />
          <DriverLicense />
        </div>
        <div className="text-[#6e6e6e] text-[15px] mt-[10px]">
          * 관장님 정보를 등록하여야 다음 단계로 넘어갑니다. <br />* 정확하게 작성해 주세요!
        </div>
        <div className="mt-[40px] flex justify-center items-center gap-[5px]">
          <SecondBtnSM
            className="w-[111px] h-[40px] text-[16px]"
            onClick={() => navigate('/logout')}
          >
            로그아웃
          </SecondBtnSM>
          <PrimaryBtnSM className="w-[380px] h-[40px] text-[16px]" onClick={handleSubmit}>
            등록
          </PrimaryBtnSM>
        </div>
      </PageContainer>
      {isLoading || (loading && <LoadingModal />)}
    </>
  );
};

export default KwanjangInfoRegistration;
